  <script>
  export default {
    name: 'MypageEdit',
    data() {
    return {
      userInfo: {
        nickname: '', //  
        email: '',
        gender: '',
        birthdate: '',
      },
      riotInfo: {
        summonerRank: {
        tier: '',
        rank: '',
        },
        summonerInfo:{
          summonerLevel: '',
          profileIconId: '',
        },
        top5Champions: [],
      },
      showRiotModal: false,
      summonerName: '',
      tag: '',
    };
    // if (this.userInfo.password !== this.userInfo.passwordcheck) {
    //           alert('  .');
    //           return;
    //       }
  },
  mounted() {
    this.checkLoginStatus();
  },
  methods: {
    async checkLoginStatus() {
      try {
        const response = await fetch('http://localhost:3000/auth/check-login', {
          method: 'GET',
          credentials: 'include', //  
        });

        if (response.ok) {
          const data = await response.json();
          this.isLoggedIn = data.loggedIn;
          if (data.loggedIn) {
            this.userInfo = data.user || {}; //    
          }
        } else {
          this.resetUserData();
        }
      } catch (error) {
        console.error('Error checking login status:', error);
        this.resetUserData();
        }
      },

    async updateUserProfile() {
    try {
      const response = await fetch('http://localhost:3000/change-userprofile', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        credentials: 'include', //   
        body: JSON.stringify({
          nickname: this.userInfo.nickname,
          birthdate: this.userInfo.birthdate,  
          gender: this.userInfo.gender,
          email: this.userInfo.email,
        }),
      });

      if (response.ok) {
        const result = await response.json();
        if (result.success) {
          alert('   .');
        } else {
          alert(result.message || '   ');
        }
      } else {
        console.error('Error updating profile:', response.statusText);
        alert('  .');
      }
    } catch (error) {
      console.error('Error updating profile:', error);
      alert('    .');
    }
  },
  
    
    async linkRiotAccount() {
      console.log("  "); //  

      if (!this.summonerName || !this.tag) {
        alert("   .");
        return;
      }

      try {

        console.log(" :", this.summonerName);
        console.log(":", this.tag);

        console.log("  :", this.summonerName, this.tag);  //     
        const response = await fetch("http://localhost:3000/summonerInfo", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          credentials: "include",
          body: JSON.stringify({
            summonerName: this.summonerName, 
            tag: this.tag, 
          }),
        });
        console.log("   :", response.status); //   
        const result = await response.json();
        console.log("  :", result); //   

        if (result.success) {
          alert("  ");
          // Riot API  
          this.riotInfo = {
            tier: result.tier || " ",
            summonerLevel: result.summonerLevel || " ",
            profileIconId: result.profileIconId || '',
            top5Champions: result.top5Champions || [],
          };
          this.showRiotModal = false;
        } else {
          alert("  : " + result.message);
        }
      } catch (error) {
        console.error("Error linking Riot account:", error);
        alert("   .");
      }
    },
  }
};
</script>
  
