<template>
  <div>
    <!--     -->
    <div v-if="post">
      <!--    -->
      <div v-if="isEditing">
        <h1> </h1>
        <form @submit.prevent="updatePost">
          <div>
            <label for="title"></label>
            <input v-model="editedTitle" id="title" type="text" required />
          </div>
          <div>
            <label for="content"></label>
            <textarea v-model="editedContent" id="content" required></textarea>
          </div>
          <button type="submit"> </button>
          <button type="button" @click="cancelEdit"></button>
        </form>
      </div>

      <!--    -->
      <div v-else>
        <h1>{{ post.title }}</h1>
        <p v-html="convertNewLinesToBreaks(post.content)"></p>
        <small>: {{ post.author || ' ' }}</small>
        <br />
        <small> : {{ formatDate(post.createdAt) }}</small>
        <br />
        <small> : {{ post.views || 0 }}</small> <!--   -->

        <!-- /  -->
        <div>
          <button @click="likePost"> ({{ post.likes }})</button>
          <button @click="dislikePost"> ({{ post.dislikes }})</button>
        </div>

        <!--  /  -->
        <div v-if="isAuthor">
          <button @click="enterEditMode"></button>
          <button @click="deletePost"></button>
        </div>
      </div>

      <!--    -->
      <div v-if="comments.length > 0">
        <h3> ({{ comments.length }})</h3>
        <div>
          <label for="comment-sort"> :</label>
          <select id="comment-sort" v-model="sortOrder" @change="sortComments">
            <option value="latest"></option>
            <option value="oldest"></option>
            <option value="likes"></option>
          </select>
        </div>

        <!--   -->
        <ul>
          <li v-for="comment in sortedComments" :key="comment._id" class="comment-item">
            <div class="comment-header">
              <strong>{{ comment.nickname }}</strong>
              <small>{{ formatDate(comment.createdAt) }}</small>

              <!--  /  -->
              <div v-if="currentUser?.userid === comment.userId" class="comment-actions">
                <button @click="startEditingComment(comment._id, comment.content)"> </button>
                <button @click="deleteComment(comment._id)"> </button>
              </div>
            </div>

            <!--    -->
            <div v-if="editingCommentId === comment._id">
              <textarea v-model="editingContent"></textarea>
              <button @click="saveEditedComment(comment._id)"></button>
              <button @click="cancelEditing"></button>
            </div>

            <!--   -->
            <div v-else>
              <p v-html="convertNewLinesToBreaks(comment.content)"></p>
            </div>
            <!--  /  -->
            <div>
              <button @click="likeComment(comment._id)"> ({{ comment.likes || 0 }})</button>
              <button @click="dislikeComment(comment._id)"> ({{ comment.dislikes || 0 }})</button>
            </div>
          </li>
        </ul>
      </div>

      <!--   -->
      <div v-if="currentUser">
        <textarea v-model="newComment" placeholder=" "></textarea>
        <button @click="submitComment"> </button>
      </div>
    </div>

    <!--   -->
    <div v-else-if="loading">
      <h2> ...</h2>
    </div>

    <!--   -->
    <div v-else>
      <h2>  .</h2>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  props: ['id'], //  ID props 
  data() {
    return {
      post: null, //  
      currentUser: null, //    
      loading: true, //  
      isEditing: false, //    
      editedTitle: '', //   
      editedContent: '', //   
      comments: [], //  
      newComment: '', //   
      editingCommentId: null, //    ID
      editingContent: '', //    
      sortOrder: 'latest', //   
    };
  },
  computed: {
    isAuthor() {
      return this.currentUser?.nickname === this.post?.author;
    },
    sortedComments() {
      return [...this.comments].sort((a, b) => {
        if (this.sortOrder === 'latest') {
          return new Date(b.createdAt) - new Date(a.createdAt); // 
        } else if (this.sortOrder === 'oldest') {
          return new Date(a.createdAt) - new Date(b.createdAt); // 
        } else if (this.sortOrder === 'likes') {
          return (b.likes || 0) - (a.likes || 0); // 
        }
      });
    },
  },
  methods: {
    async loadData() {
      this.loading = true;
      try {
        await Promise.all([this.fetchPost(), this.fetchComments(), this.fetchCurrentUser()]);
      } catch (error) {
        console.error('    :', error);
      } finally {
        this.loading = false;
      }
    },
    async fetchPost() {
      try {
        const response = await axios.get(`http://localhost:3000/api/board/${this.id}`, { withCredentials: true });
        this.post = response.data;
        this.editedTitle = this.post.title;
        this.editedContent = this.post.content;
      } catch (error) {
        console.error('    :', error);
        this.post = null;
      }
    },
    async fetchComments() {
      try {
        const response = await axios.get(`http://localhost:3000/api/board/${this.id}/comments`, { withCredentials: true });
        this.comments = response.data;
      } catch (error) {
        console.error('    :', error);
      }
    },
    async fetchCurrentUser() {
      try {
        const response = await axios.get('http://localhost:3000/auth/check-login', { withCredentials: true });
        if (response.data.loggedIn) {
          this.currentUser = response.data.user;
        }
      } catch (error) {
        console.error('     :', error);
        this.currentUser = null;
      }
    },
    formatDate(dateString) {
      const date = new Date(dateString);
      return date.toLocaleDateString('ko-KR', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit',
      });
    },
    //    
    enterEditMode() {
      this.isEditing = true;
    },
    cancelEdit() {
      this.isEditing = false;
      this.editedTitle = this.post.title;
      this.editedContent = this.post.content;
    },
    async updatePost() {
      try {
        const updatedData = {
          title: this.editedTitle,
          content: this.editedContent,
        };
        await axios.put(`http://localhost:3000/api/board/${this.id}`, updatedData, { withCredentials: true });
        this.post.title = this.editedTitle;
        this.post.content = this.editedContent;
        this.isEditing = false;
        alert(' .');
      } catch (error) {
        console.error('    :', error);
        alert('  .');
      }
    },
    async deletePost() {
      try {
        await axios.delete(`http://localhost:3000/api/board/${this.id}`, { withCredentials: true });
        alert(' .'); //  
        this.$router.push('/board');
      } catch (error) {
        console.error('    :', error);
      }
    },
    async likePost() {
      if (!this.currentUser) {
        alert(' .');
        return;
      }
      try {
        const response = await axios.put(
          `http://localhost:3000/api/board/${this.id}/like`,
          { action: 'like' },
          { withCredentials: true }
        );
        this.post.likes = response.data.likes;
        this.post.dislikes = response.data.dislikes;
      } catch (error) {
        console.error('    :', error);
        alert('  .');
      }
    },
    async dislikePost() {
      if (!this.currentUser) {
        alert(' .');
        return;
      }
      try {
        const response = await axios.put(
            `http://localhost:3000/api/board/${this.id}/like`,
            { action: 'dislike' },
            { withCredentials: true }
        );
        this.post.likes = response.data.likes;
        this.post.dislikes = response.data.dislikes;
      } catch (error) {
        console.error('    :', error);
        alert('  .');
      }
    },
    //    
    startEditingComment(commentId, content) {
      this.editingCommentId = commentId;
      this.editingContent = content;
    },
    cancelEditing() {
      this.editingCommentId = null;
      this.editingContent = '';
    },
    async saveEditedComment(commentId) {
      if (!this.editingContent.trim()) {
        alert('  .');
        return;
      }

      try {
        await axios.put(
          `http://localhost:3000/api/board/comments/${commentId}`,
          { content: this.editingContent },
          { withCredentials: true }
        );
        const comment = this.comments.find((c) => c._id === commentId);
        if (comment) {
          comment.content = this.editingContent;
        }
        this.cancelEditing();
        alert(' .');
      } catch (error) {
        console.error('    :', error);
        alert('  .');
      }
    },
    async deleteComment(commentId) {
      try {
        await axios.delete(`http://localhost:3000/api/board/comments/${commentId}`, { withCredentials: true });
        this.comments = this.comments.filter((comment) => comment._id !== commentId);
        alert(' .'); //  
      } catch (error) {
        console.error('    :', error);
        alert('  .');
      }
    },
    async submitComment() {
      if (!this.newComment.trim()) {
        alert('  .');
        return;
      }

      try {
        const response = await axios.post(
          `http://localhost:3000/api/board/${this.id}/comments`,
          { content: this.newComment },
          { withCredentials: true }
        );
        this.comments.push(response.data);
        this.newComment = '';
      } catch (error) {
        console.error('    :', error);
        alert('  .');
      }
    },
    async likeComment(commentId) {
      if (!this.currentUser) {
        //    
        alert(' .');
        return;
      }
      try {
        await axios.put(
          `http://localhost:3000/api/board/comments/${commentId}/like`,
          { action: 'like' },
          { withCredentials: true }
        );
        await this.fetchComments(); //   
      } catch (error) {
        console.error('     :', error);
      }
    },
    async dislikeComment(commentId) {
      if (!this.currentUser) {
        //    
        alert(' .');
        return;
      }
      try {
        await axios.put(
          `http://localhost:3000/api/board/comments/${commentId}/like`,
          { action: 'dislike' },
          { withCredentials: true }
        );
        await this.fetchComments(); //   
      } catch (error) {
        console.error('     :', error);
      }
    },
    sortComments() {
      console.log(`  ${this.sortOrder} .`);
    },
    
    async incrementViews() {
      try {
        const response = await axios.post(`http://localhost:3000/api/board/${this.id}/views`, {}, { withCredentials: true });
        if (response.status === 200) {
          //       
          this.post.views += 1;
        }
      } catch (error) {
        console.error('     :', error);
      }
    },
    convertNewLinesToBreaks(text) {
      return text.replace(/\n/g, '<br>');
    },
  },
  created() {
    this.loadData().then(() => {
      this.incrementViews();
    });
  },
};
</script>

<style>
.comment-item {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}
.comment-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>