<template>
  <div>
    <h1></h1>

    <!--   (    ) -->
    <div v-if="currentPage === 'board'">
      <label for="search">:</label>
      <input v-model="searchQuery" @keyup.enter="filterPosts" type="text" id="search" placeholder=" " />
      
      <!--    -->
      <select v-model="searchType">
        <option value="title"></option>
        <option value="content"></option>
        <option value="author"></option>
      </select>

      <button @click="resetSearch"></button> <!--    -->
    </div>

    <!--   (    ) -->
    <div v-if="currentPage === 'board'">
      <label for="sort">:</label>
      <select id="sort" v-model="sortOrder" @change="sortPosts">
        <option value="latest"></option>
        <option value="oldest"></option>
        <option value="likes"></option>
        <option value="views"></option>
      </select>
    </div>

    <!--    -->
    <button @click="goToWritePage" v-if="currentPage === 'board'"> </button>

    <!--   -->
    <div v-if="currentPage === 'board'">
      <div v-if="loading"> ...</div>
      <div v-else-if="error" class="error">   .</div>
      <ul v-else-if="sortedPosts.length">
        <li v-for="post in sortedPosts" :key="post._id">
          <h2 @click="goToDetailPage(post._id)">{{ post.title }}</h2>
          <p v-html="convertNewLinesToBreaks(post.content)"></p>
          <small>{{ post.author || ' ' }} - {{ formatDate(post.createdAt) }}</small>
          <div>
            <span> {{ post.likes || 0 }}</span> | <span> {{ post.dislikes || 0 }}</span>
          </div>
          <div>
             : {{ post.views || 0 }}
          </div>
        </li>
      </ul>
      <div v-else>
         .
      </div>
    </div>

    <!--    -->
    <div v-if="currentPage === 'write'">
      <h2> </h2>
      <form @submit.prevent="submitPost">
        <div>
          <label for="title"></label>
          <input v-model="title" type="text" id="title" required />
        </div>
        <div>
          <label for="content"></label>
          <textarea v-model="content" id="content" required></textarea>
        </div>
        <button type="submit"> </button>
        <button type="button" @click="goToBoardPage"></button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      posts: [], //  
      filteredPosts: [], //   
      loading: true, //  
      error: false, //  
      title: '', //  
      content: '', //  
      currentPage: 'board', //    ('board', 'write')
      currentUser: null, //    
      sortOrder: 'latest', //   ('latest' or 'oldest')
      searchQuery: '', // 
      searchType: 'title', //   (, , )
    };
  },
  computed: {
    //         
    sortedPosts() {
      return [...this.filteredPosts].sort((a, b) => {
        if (this.sortOrder === 'latest') {
          return new Date(b.createdAt) - new Date(a.createdAt); // 
        } else if (this.sortOrder === 'oldest') {
          return new Date(a.createdAt) - new Date(b.createdAt); // 
        } else if (this.sortOrder === 'likes') {
          return (b.likes || 0) - (a.likes || 0); // 
        } else if (this.sortOrder === 'views') {
          return (b.views || 0) - (a.views || 0); // 
        }
      });
    }
  },
  created() {
    this.initData(); //   
    this.checkLoginStatus(); //   
  },
  methods: {
    //   
    async checkLoginStatus() {
      try {
        const response = await axios.get('http://localhost:3000/auth/check-login', { withCredentials: true });
        this.currentUser = response.data.user || null; //    
      } catch (error) {
        console.error('     :', error);
        this.currentUser = null; //  
      }
    },
    //   
    async initData() {
      this.loading = true;
      this.error = false;
      try {
        await this.fetchPosts();
      } catch (error) {
        console.error('     :', error);
        this.error = true;
      } finally {
        this.loading = false;
      }
    },

    //   
    async fetchPosts() {
      try {
        const response = await axios.get('http://localhost:3000/api/board', { withCredentials: true });
        this.posts = response.data;
        this.filteredPosts = [...this.posts]; //      
      } catch (error) {
        console.error('    :', error);
        this.error = true;
      }
    },

    //   
    filterPosts() {
      if (this.searchQuery.trim()) {
        if (this.searchType === 'title') {
          this.filteredPosts = this.posts.filter(post =>
            post.title.toLowerCase().includes(this.searchQuery.toLowerCase())
          );
        } else if (this.searchType === 'content') {
          this.filteredPosts = this.posts.filter(post =>
            post.content.toLowerCase().includes(this.searchQuery.toLowerCase())
          );
        } else if (this.searchType === 'author') {
          this.filteredPosts = this.posts.filter(post =>
            post.author.toLowerCase().includes(this.searchQuery.toLowerCase())
          );
        }
      } else {
        this.filteredPosts = [...this.posts]; //     
      }
    },

    //   
    async submitPost() {
      try {
        await axios.post('http://localhost:3000/api/board', { title: this.title, content: this.content }, { withCredentials: true });
        this.goToBoardPage();
        this.initData();
      } catch (error) {
        console.error('    :', error);
      }
    },

    //   
    goToBoardPage() {
      this.currentPage = 'board';
      this.title = '';
      this.content = '';
    },
    goToWritePage() {
      if (!this.currentUser) {
        alert(' .');
        return;
      }
      this.currentPage = 'write';
    },
    goToDetailPage(postId) {
      this.$router.push({ name: 'BoardDetail', params: { id: postId } });
    },

    //   
    formatDate(dateString) {
      return new Date(dateString).toLocaleDateString(undefined, {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    },

    //        
    resetSearch() {
      this.searchQuery = ''; //  
      this.filteredPosts = [...this.posts]; //    
    },
    convertNewLinesToBreaks(text) {
      return text.replace(/\n/g, '<br>');
    },
  }
};
</script>

<style scoped>
.error {
  color: red;
}
button {
  margin: 5px;
}
</style>