<script>
export default {
  name: 'App',
  data() {
    return {
      mypageopen: false,
      isLoggedIn: false, //  
      userInfo: {},
    };
  },


  computed: {
    // ,    
    isAuthPage() {
      return ['/login', '/signup', '/find-password', '/find-id', '/mypage-edit', '/matchqueue', '/chatroom', '/board', '/patch-notes'].includes(this.$route.path);
    },
    //    
    showHeader() {
      return !this.isAuthPage;
    },
    showFooter() {
      return !this.isAuthPage;
    },
    //     
    showPlaceholder() {
      return !this.isAuthPage;
    },
    videoElement() {
      return this.$refs.videoElement;
    }
  },
  methods: {
    enterMatchQueue() {
      this.$router.push('/matchqueue');
    },
    playVideo() {
      this.videoElement.play();
    },
    pauseVideo() {
      this.videoElement.pause();
    },
    async checkLoginStatus() {
      try {
        const response = await fetch('http://localhost:3000/auth/check-login', {
          method: 'GET',
          credentials: 'include', //  
        });

        if (response.ok) {
          const data = await response.json();
          this.isLoggedIn = data.loggedIn;
          if (data.loggedIn) {
            this.userInfo = data.user || {}; //    
          }
        } else {
          this.resetUserData();
        }
      } catch (error) {
        console.error('Error checking login status:', error);
        this.resetUserData();
      }
    },
    resetUserData() {
      this.isLoggedIn = false;
      this.userInfo = {}; //   
    },
    async logout() {
      try {
        const response = await fetch('http://localhost:3000/logout', {
          method: 'GET',
          credentials: 'include',
        });

        if (response.ok) {
          this.isLoggedIn = false;
          this.nickname = '';
          this.$router.push('/'); //     
        }
      } catch (error) {
        console.error('Error logging out:', error);
      }
    },
    async withdraw() {
      const confirmation = confirm('  ?');
      if (!confirmation) return;

      try {
        const response = await fetch('http://localhost:3000/withdraw', {
          method: 'GET', // DELETE   (   )
          credentials: 'include',
        });

        if (response.ok) {
          alert(' .');
          this.resetUserData();
          this.$router.push('/'); //   
        } else {
          alert('   .  .');
        }
      } catch (error) {
        console.error('Error withdrawing: ', error);
        alert('   .  .');
      }
    },
  },
  mounted() {
    this.checkLoginStatus(); //      
  },
};
</script>

