<template>
  <div id="app">
  <div class="signup-container">
    <header class="header">
      <div class="logo">
      <div class="circle"></div>
      <span></span>
      </div>
      <nav>
        <a href="/"> </a>
        <a href="/login"></a>
        <a href="/contact">Contact</a>
      </nav>
    </header>
    <main>
      <div class="form-container">
        <h2> </h2>
        <p>    . </p>
        <form @submit.prevent="handleSubmit">
          <div class="form-group">
            <label for="userid"></label>
            <input id="userid" type="text" placeholder=" " v-model="form.userid" @input="checkDuplicateUserId" required />
            <div :style="{ color: duplicateCheck.color }">{{ duplicateCheck.message }}</div>
          </div>
          <div class="form-group password-field">
  <label for="password"></label>
  <div class="password-container">
    <input
      id="password"
      :type="passwordVisible ? 'text' : 'password'"
      placeholder=" "
      v-model="form.password"
      required
    />
    <img
      :src="passwordVisible ? openIcon : closeIcon"
      alt="Show Password"
      class="toggle-password"
      @click="togglePasswordVisibility('password')"
    />
  </div>
</div>
          <div class="form-group password-field">
            <label for="passwordcheck"> </label>
            <div class="password-container">
              <input
      id="passwordcheck"
      :type="passwordCheckVisible ? 'text' : 'password'"
      placeholder="  "
      v-model="form.passwordcheck"
      required
    />
    <img
      :src="passwordCheckVisible ? openIcon : closeIcon"
      alt="Show Password"
      class="toggle-password"
      @click="togglePasswordVisibility('passwordcheck')"
    />
  </div>
          </div>
          <div class="form-group">
            <label for="email"></label>
            <input id="email" type="email" v-model="form.email" placeholder=" (    )" />
          </div>
          <div class="form-group">
            <label for="nickname"></label>
            <input id="nickname" type="text" placeholder=" " v-model="form.nickname" required />
          </div>
          <!-- <div class="form-group">
            <label for="birthdate"></label>
            <input id="birthdate" type="date" v-model="form.birthdate" placeholder=" " />
          </div> -->
          <!-- <div class="form-group">
            <label for="gender"></label>
            <select id="gender" v-model="form.gender" require>
              <option value="male"></option>
              <option value="female"></option>
              <option value="other"></option>
            </select>
          </div> -->
          <div class="button-group">
            <button type="button" class="cancel-button">Cancel</button>
            <button type="submit" class="signup-button">Sign Up</button>
          </div>
          </form>
          </div>
          </main>
      </div>
  </div>
</template>



<script>
export default {
  data() {
      return {
          form: {
              userid: '',
              password: '',
              passwordcheck: '',
              email: '',
              nickname: '',
              birthdate: '',
              gender: 'other',
          },
          duplicateCheck: {
              message: '',
              color: '',
          },
          passwordVisible: false,
          passwordCheckVisible: false,
          openIcon: require('@/assets/open.png'), 
      closeIcon: require('@/assets/close.png'),
      };
  },
  methods: {
    togglePasswordVisibility(field) {
      if (field === 'password') {
      this.passwordVisible = !this.passwordVisible;
    } else if (field === 'passwordcheck') {
      this.passwordCheckVisible = !this.passwordCheckVisible;
    }
    },
      async checkDuplicateUserId() {
          const userid = this.form.userid;

          if (!userid) {
              this.duplicateCheck.message = '';
              return;
          }

          try {
              const response = await fetch('http://localhost:3000/check-duplicate', {
                  method: 'POST',
                  headers: { 'Content-Type': 'application/json' },
                  credentials: 'include', //   
                  body: JSON.stringify({ userid }),
              });

              const result = await response.json();
              if (response.ok) {
                  this.duplicateCheck.message = result.message;
                  this.duplicateCheck.color = 'green';
              } else {
                  this.duplicateCheck.message = result.message;
                  this.duplicateCheck.color = 'red';
              }
          } catch (error) {
              console.error('Error checking duplicate:', error);
              this.duplicateCheck.message = '    .';
              this.duplicateCheck.color = 'red';
          }
      },
      async handleSubmit() {
        const { userid, password, passwordcheck, nickname, email } = this.form;

    if (!userid || !password || !passwordcheck || !nickname || !email) {
        alert('  .');
        return;
    }
          if (this.form.password !== this.form.passwordcheck) {
              alert('  .');
              return;
          }

          try {
              const response = await fetch('http://localhost:3000/signup', {
                  method: 'POST',
                  headers: { 'Content-Type': 'application/json' },
                  body: JSON.stringify(this.form),
              });

              const result = await response.json();
              if (response.ok) {
                  alert(result.message);
                  window.location.href = '/';
              } else {
                  alert(result.message);
              }
          } catch (error) {
              console.error('Signup error:', error);
              alert('   .');
          }
      },
    },
    
};
</script>

