import { createRouter, createWebHistory } from 'vue-router';
import SignupForm from '../components/SignupForm.vue';
import LoginForm from '@/components/LoginForm.vue';
import RequestPasswordReset from '@/components/RequestPasswordReset.vue';
import ResetPassword from '@/components/ResetPassword.vue';
import FindPassword from '@/components/FindPassword.vue';
import FindId from '@/components/FindId.vue';
import MypageEdit from '@/components/MypageEdit.vue';
import BoardForm from '@/components/BoardForm.vue'; // BoardForm  
import BoardDetail from '@/components/BoardDetail.vue'; //BoardDetail  
import patchNotesDisplay from '@/components/patchNotesDisplay.vue'; //   
import MatchQueue from '@/components/MatchQueue.vue';
import ChatRoom from '@/components/ChatRoom.vue';
import axios from 'axios';

const routes = [
  {
    path: '/signup',
    name: 'Signup',
    component: SignupForm,
  },
  {
    path: '/login',
    name: 'Login',
    component: LoginForm,
  },
  { path: '/request-password-reset', component: RequestPasswordReset },
  { path: '/reset-password', component: ResetPassword },
  { path: '/find-password', component: FindPassword },
  { path: '/find-id', component: FindId },
  { path: '/mypage-edit', component: MypageEdit },
  {
    path: '/board',
    name: 'BoardForm',
    component: BoardForm, //   
  },
  {
    path: '/board/:id',
    name: 'BoardDetail',
    component: BoardDetail,
    props: true, //  ID  
  },
  { path: '/matchqueue', component: MatchQueue },
  {
    path: '/chatroom',
    name: 'ChatRoom',
    component: ChatRoom,
    props: true, // params  props 
  },
  {
    path: '/patch-notes',
    name: 'PatchNotesDisplay',
    component: patchNotesDisplay,  //   
  },

];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

async function isLoggedIn() {
  try {
    const response = await axios.get('http://localhost:3000/auth/check-login', {
      withCredentials: true, //   
    });
    return response.data.loggedIn;
  } catch (error) {
    console.error('Error checking login status:', error);
    return false; //      
  }
}

// Navigation Guard 
router.beforeEach(async (to, from, next) => {
  const requiresAuth = to.meta.requiresAuth;

  if (requiresAuth) {
    //        
    const loggedIn = await isLoggedIn();

    if (!loggedIn) {
      //      
      return next({ name: 'Login', query: { redirect: to.fullPath } });
    }
  }

  next(); //        
});

export default router;