<template>
    <div id="app">
    <div class="find-password-page">
      <!--   -->
      <header class="header">
        <div class="logo">
            <div class="circle"></div>
             </div>
        <nav class="nav-links">
          <a href="/"> </a>
          <a href="/login"></a>
          <a href="/contact" >Contact</a>
        </nav>
      </header>
  
      <!--    -->
      <main class="main-content">
        <h1 class="title"> </h1>
        <form class="form">
          <div class="form-group">
            <label for="newPassword" class="form-label"> </label>
            <input
              type="password"
              id="newPassword"
              class="form-input"
              v-model="newPassword"
              placeholder="  "
            />
          </div>
          <div class="form-group">
            <label for="confirmPassword" class="form-label">  </label>
            <input
              type="password"
              id="confirmPassword"
              class="form-input"
              v-model="confirmPassword"
              placeholder="   "
            />
          </div>
          <p v-if="message" :class="success ? 'success' : 'error'">{{ message }}</p>
          <div class="button-group">
            <button type="button" class="cancel-button"></button>
            <button type="submit" class="submit-button"></button>
          </div>
        </form>
      </main>
    </div>
    </div>
</template>
  
<script>
  export default {
    data() {
        return {
            newPassword: '',
            confirmPassword: '',
            message: '',
            success: false,
        };
    },
    methods: {
        async resetPassword() {
            const token = new URLSearchParams(window.location.search).get('token');
            if (!token) {
                this.success = false;
                this.message = '  .';
                return;
            }

            if (this.newPassword !== this.confirmPassword) {
                this.success = false;
                this.message = '  .';
                return;
            }

            try {
                const response = await fetch('http://localhost:3000/reset-password', {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ token, newPassword: this.newPassword }),
                });

                const result = await response.json();
                this.success = result.success;
                this.message = result.message;
            } catch (error) {
                console.error('Error resetting password:', error);
                this.success = false;
                this.message = '    .';
            }
        },
    },
  };
</script>
  
