<template>
    
    <!--    -->
    <div class="chat-room">
           <div class="chat-header">
               <h1></h1>
               <button @click="leaveRoom" class="leave-button"></button>
           </div>

           <div class="opponent-info" v-if="getOpponent && getOpponent.nickname">
   <h2>{{ getOpponent.nickname }}</h2>
   <p>: {{ getOpponent.position || ' ' }}</p>
   <p>: {{ getOpponent.microphone || ' ' }}</p>
   <p>: {{ getOpponent.summonerRank.tier || ' ' }}</p>
</div>
       <div v-else>
           <p>    ...</p>
       </div>

       <div class="chat-window" ref="chatWindow">
           <div v-for="(message, index) in messages" :key="index" class="chat-message" :class="{
               'my-message': message.username === userInfo?.nickname,
               'system-message': message.type === 'system'
           }">
               <div class="message-content" :class="{ 'system-content': message.type === 'system' }">
                   <span v-if="message.type !== 'system'" class="username">{{ message.username }}</span>
                   <span class="message-text">{{ message.message }}</span>
               </div>
           </div>
       </div>

       <div class="chat-input">
           <input v-model="newMessage" @keyup.enter="sendMessage" placeholder=" ..." ref="messageInput" />
           <button @click="sendMessage"></button>
       </div>
   </div>
</template>

<script>
import { io } from "socket.io-client";

export default {
    data() {
        return {
            socket: null,
            messages: [],
            newMessage: "",
            match: null,
            matchId: null,
            userInfo: null
        };
    },
    computed: {
        getOpponent() {
            if (!this.match || !this.match.players || !this.userInfo) return {};
            return this.match.players.find(player => player.userid !== this.userInfo.userid) || this.match.players[0] || {};
        }
    },
    watch: {
        messages: {
            handler() {
                this.$nextTick(() => {
                    this.scrollToBottom();
                });
            },
            deep: true
        },
        match: {
            handler(newMatch) {
                if (newMatch && newMatch.roomName && !this.socket) {
                    this.setupSocket();
                }
            },
            deep: true
        }
    },
    methods: {
        setupSocket() {
            if (this.socket) return;

            console.log("   ");
            this.socket = io("http://localhost:3000", { withCredentials: true });

            //    
            this.socket.on("connect", () => {
                console.log("  :", this.socket.id);
                if (this.match && this.match.roomName) {
                    console.log("   :", this.match.roomName);
                    this.socket.emit("join room", { roomName: this.match.roomName });
                }
            });

            //    
            this.socket.on("chat message", (data) => {
                console.log("  :", data);
                this.messages.push(data);
            });

            this.socket.on("user disconnected", (data) => {
                console.log("   :", data);
                this.opponentDisconnected = true;
                this.messages.push({
                    type: 'system',
                    message: `${data.nickname}  .`
                });

                // 3    
                setTimeout(() => {
                    this.$router.push('/match');
                }, 3000);
            });

            //   
            this.socket.on("connect_error", (error) => {
                console.error("   :", error);
            });
        },

        leaveRoom() {
            if (this.socket && this.matchId) {
                this.socket.emit("leave room", {
                    matchId: this.matchId,
                    userId: this.userInfo.userid,
                    nickname: this.userInfo.nickname
                });
                this.socket.disconnect();
            }
            this.$router.push('/match');
        },

        sendMessage() {
            if (this.newMessage.trim() && this.socket && this.matchId) {
                console.log("   :", {
                    matchId: this.matchId,
                    message: this.newMessage
                });

                this.socket.emit("chat message", {
                    matchId: this.matchId,
                    message: this.newMessage
                });
                this.newMessage = "";
            } else {
                console.warn("   :", {
                    hasSocket: !!this.socket,
                    hasMatchId: !!this.matchId,
                    messageLength: this.newMessage.length
                });
            }
        },
        scrollToBottom() {
            const chatWindow = this.$refs.chatWindow;
            if (chatWindow) {
                chatWindow.scrollTop = chatWindow.scrollHeight;
            }
        },
        async fetchUserInfo() {
            try {
                const userResponse = await fetch('http://localhost:3000/auth/check-login', {
                    credentials: 'include'
                });
                const userData = await userResponse.json();

                if (userData.loggedIn) {
                    this.userInfo = userData.user;
                    console.log("   :", this.userInfo);
                } else {
                    console.error("     .");
                }
            } catch (error) {
                console.error("    :", error);
            }
        },
        async fetchMatchInfo() {
            if (!this.matchId) {
                console.error(" matchId !");
                return;
            }

            try {
                const response = await fetch(`http://localhost:3000/match/get/${this.matchId}`, {
                    method: "GET",
                    credentials: "include",
                });

                const data = await response.json();
                console.log("    :", data);

                if (data.success) {
                    this.match = data.match;
                } else {
                    console.error("     .");
                }
            } catch (error) {
                console.error("    :", error);
            }
        }
    },
    async mounted() {
        this.matchId = this.$route.query.matchId;
        console.log(" ChatRoom  matchId:", this.matchId);

        await this.fetchUserInfo();
        await this.fetchMatchInfo();
        this.setupSocket();
    },
    beforeUnmount() {
        if (this.socket) {
            if (!this.opponentDisconnected) {
                this.socket.emit("leave room", {
                    matchId: this.matchId,
                    userId: this.userInfo.userid,
                    nickname: this.userInfo.nickname
                });
            }
            console.log("   ");
            this.socket.disconnect();
            this.socket = null;
        }
    }
};
</script>

