<template>
    <div class="match-queue">
        <div class="match-container">
            <!--   &   &       -->
            <div class="profile-rank-container">
                <!--  -->
                <div class="profile-section">
                    <div class="profile-picture">
                        <!--     -->
                        <img :src="`http://ddragon.leagueoflegends.com/cdn/14.22.1/img/profileicon/${userInfo.summonerInfo?.profileIconId}.png`" alt=" " class="summoner-icon" />
                    </div>
                    <span>, {{ userInfo.nickname }}!</span>
                    <!--   &    -->
                <div class="user-rank-container">
                    <div class="rank-info">
                        <img :src="`https://raw.communitydragon.org/latest/plugins/rcp-fe-lol-shared-components/global/default/${userInfo.summonerRank?.tier.toLowerCase()}.png`" alt=" " class="rank-icon" />
                        <span class="rank-text">{{ userInfo.summonerRank?.tier }}</span>
                    </div>
                    <button class="refresh-button" @click="fetchLatestMatchData"> </button>
                </div>
                </div>
    
                
            </div>
    
                <!--   -->
                <div class="selection-section">
                    <h2> ( 2 )</h2>
                    <div class="position-options">
                        <div v-for="pos in positions" :key="pos.value"
                            :class="{ selected: selectedPositions.includes(pos.value) }" @click="togglePosition(pos.value)">
                            <img :src="pos.icon" :alt="pos.label" />
                            <span>{{ pos.label }}</span>
                        </div>
                    </div>
                </div>
    
                <!--     -->
                <div class="selection-section">
                    <h2>   </h2>
                    <div class="voice-options">
                        <div v-for="voice in voiceOptions" :key="voice.value"
                            :class="{ active: microphone === voice.value }" @click="microphone = voice.value">
                            <img :src="voice.icon" :alt="voice.label" />
                            <span>{{ voice.label }}</span>
                        </div>
                    </div>
                </div>
    
                <!-- /  -->
                <div class="selection-section">
                    <h2> / </h2>
                    <div class="game-mode-options">
                        <div v-for="mode in gameModes" :key="mode.value" :class="{ active: matchType === mode.value }"
                            @click="matchType = mode.value">
                            <img :src="mode.icon" :alt="mode.label" />
                            <span>{{ mode.label }}</span>
                        </div>
                    </div>
                </div>
    
                <!--   -->
                <button @click="startMatching" :disabled="isMatching" class="match-button"> </button>
            </div>
    
            <!--     -->
            <div class="match-confirmation" v-if="matchFound">
    <div class="match-info">
        <p class="match-text"> !!</p>
    </div>
    <div class="match-buttons">
        <button class="reject-button" @click="rejectMatch"></button>
        <button class="accept-button" @click="acceptMatch"></button>
    </div>
</div>
    
            <!--     UI  -->
            <div class="popup-overlay" v-if="isMatching && !matchFound">
                <div class="popup-content">
                    <img src="/icons/loading.png" alt="Loading" class="loading-icon" />
                    <p class="waiting-time">{{ formattedTime }}</p>
                    <p>  .   .</p>
                    <button @click="cancelMatching"></button>
                </div>
            </div>
        </div>
    </template>

<script>
import { io } from "socket.io-client";

export default {
    data() {
        return {
            socket: null,
            isLoggedIn: false,
            userInfo: {},
            selectedPositions: [],
            microphone: "",
            matchType: "",
            isMatching: false,
            matchFound: false,
            matchId: null,
            waitingTime: 0,
            timer: null,

            positionIconSize: 40,
            voiceIconWidth: 50,
            voiceIconHeight: 80,
            gameModeIconSize: 120,

            positions: [
                { label: "", value: "", icon: "/icons/top.png" },
                { label: "", value: "", icon: "/icons/jungle.png" },
                { label: "", value: "", icon: "/icons/mid.png" },
                { label: "", value: "", icon: "/icons/adc.png" },
                { label: "", value: "", icon: "/icons/support.png" },
                { label: "", value: "", icon: "/icons/none.png" },
            ],

            voiceOptions: [
                { label: "", value: "", icon: "/icons/mic-on.png" },
                { label: "", value: "", icon: "/icons/mic-off.png" }
            ],

            gameModes: [
                { label: "", value: "", icon: "/icons/normal.png" },
                { label: "", value: "", icon: "/icons/rank.png" }
            ]
        };
    },


    async mounted() {
        await this.checkLoginStatus(); //   
        this.initializeSocket();
    },

    methods: {
        async checkLoginStatus() {
            try {
                const response = await fetch('http://localhost:3000/auth/check-login', {
                    method: 'GET',
                    credentials: 'include', //  
                });

                if (response.ok) {
                    const data = await response.json();
                    this.isLoggedIn = data.loggedIn;
                    if (data.loggedIn) {
                        this.userInfo = data.user || {}; //    
                        console.log(" userInfo:", this.userInfo);
                    } else {
                        this.handleUnauthenticatedUser();
                    }
                } else {
                    this.handleUnauthenticatedUser();
                }
            } catch (error) {
                console.error('    :', error);
                this.handleUnauthenticatedUser();
            }
        },

        handleUnauthenticatedUser() {
            this.isLoggedIn = false;
            this.userInfo = {}; //   
            alert(" .   .");
            this.$router.push("/"); //   
        },

        resetUserData() {
            this.isLoggedIn = false;
            this.userInfo = {}; //   
        },

        initializeSocket() {
            this.socket = io("http://localhost:3000", { withCredentials: true });

            this.socket.on("disconnect", () => {
                console.log("   .  ");
                this.isMatching = false;
                this.matchFound = false;
                if (this.timer) {
                    clearInterval(this.timer);
                }
            });

            this.socket.on("matchSuccess", (data) => {
                console.log("   matchSuccess :", data);
                this.matchId = data.matchId;
                this.matchFound = true;
            });

            this.socket.on("matchRejected", (data) => {
                console.log("  :", data.message);
                this.matchFound = false;
                this.isMatching = false;
                if (this.timer) {
                    clearInterval(this.timer);
                }
                alert("   .  !");
            });

            this.socket.on("matchCancelled", (data) => {
                console.log("  :", data.message);
                this.matchFound = false;
                this.isMatching = false;
                if (this.timer) {
                    clearInterval(this.timer);
                }
            });

            this.socket.on("matchConfirmed", async (data) => {
                try {
                    const response = await fetch(`http://localhost:3000/match/save`, {
                        method: "POST",
                        headers: { "Content-Type": "application/json" },
                        credentials: "include",
                        body: JSON.stringify({
                            matchId: data.matchId
                        }),
                    });

                    const result = await response.json();
                    if (result.success) {
                        console.log("   ");
                        this.$router.push(`/chatroom?matchId=${data.matchId}`);
                    }
                } catch (error) {
                    console.error("   :", error);
                }
            });
        },

        togglePosition(position) {
            if (this.selectedPositions.includes(position)) {
                this.selectedPositions = this.selectedPositions.filter(p => p !== position);
            } else if (this.selectedPositions.length < 2) {
                this.selectedPositions.push(position);
            }
        },

        startMatching() {
            this.isMatching = true;
            this.waitingTime = 0;

            this.timer = setInterval(() => {
                this.waitingTime++;
            }, 1000);

            this.socket.emit(
                this.matchType === "" ? "request normalmatch" : "request rankmatch",
                {
                    position: this.selectedPositions,
                    microphone: this.microphone
                }
            );
        },

        acceptMatch() {
            if (this.matchId) {
                this.socket.emit("acceptMatch", { matchId: this.matchId });
            }
            if (this.timer) {
                clearInterval(this.timer);
            }
            this.isMatching = false;
            this.matchFound = false;
        },

        rejectMatch() {
            if (this.matchId) {
                this.socket.emit("rejectMatch", { matchId: this.matchId });
            }
            if (this.timer) {
                clearInterval(this.timer);
            }
            this.isMatching = false;
            this.matchFound = false;
        },

        cancelMatching() {
            if (this.timer) {
                clearInterval(this.timer);
            }
            this.isMatching = false;
            this.socket.emit("cancel match");

        }
    },

    computed: {
        formattedTime() {
            const minutes = Math.floor(this.waitingTime / 60);
            const seconds = this.waitingTime % 60;
            return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
        }
    },

    beforeUnmount() {
        if (this.timer) {
            clearInterval(this.timer);
        }
        if (this.socket) {
            this.socket.disconnect();
        }
    }
};
</script>

